import qs from "qs";
import { $helper } from "./https";

// 
let WikiPost = {
    // 
    newest(type, id, supply = 1, client = "std") {
        if (!id) return;
        return $helper()({
            method: "GET",
            url: `/api/wiki/post`,
            params: { type: type, source_id: id, supply: supply, client },
        });
    },

    // 
    newests(type) {
        return $helper()({
            method: "GET",
            url: `/api/wiki/posts/newest`,
            params: { type: type },
        });
    },

    // 
    versions(type, id) {
        if (!id) return;
        return $helper()({
            method: "GET",
            url: `/api/wiki/post/versions`,
            params: { type: type, source_id: id },
        });
    },

    // 
    view(post_id, params = {}) {
        if (!post_id) return null;
        return $helper()({
            method: "GET",
            url: `/api/wiki/post/${post_id}`,
            params: params,
        });
    },

    // /
    save(params) {
        return $helper()({
            method: "POST",
            url: `/api/wiki/post`,
            data: qs.stringify({ post: params }),
        });
    },

    // 
    myList(params) {
        return $helper()({
            method: "GET",
            url: `/api/my/wiki/posts`,
            params: params,
        });
    },

    // 
    myRemove(post_id) {
        if (!post_id) return null;
        return $helper()({
            method: "PUT",
            url: `/api/my/wiki/post/${post_id}/remove`,
        });
    },
};

// 
let WikiComment = {
    // 
    list(type, id) {
        if (!id) return;
        return $helper()({
            method: "GET",
            url: `/api/wiki/comments`,
            params: { type: type, source_id: id },
        });
    },

    // 
    save(params) {
        return $helper()({
            method: "POST",
            url: `/api/wiki/comment`,
            data: qs.stringify({ comment: params }),
        });
    },

    // 
    myList(params) {
        return $helper()({
            method: "GET",
            url: `/api/my/wiki/comments`,
            params: params,
        });
    },

    // 
    myRemove(comment_id) {
        if (!comment_id) return null;
        return $helper()({
            method: "PUT",
            url: `/api/my/wiki/comment/${comment_id}/remove`,
        });
    },
};

// 
function getBread(channel) {
    return $helper().get("/api/breadcrumb/" + channel, {});
}

export { WikiPost, WikiComment, getBread };
